.background {
    background: #151B25;
    text-align: center;
    height: 100%;
}

.main{
    height: 100%;
    background-color: #19212E;
    margin: 0 1%;
    overflow: auto; 
}

.main h1 {
    font-family: "M PLUS Rounded 1c";
    color: #E0E1DD;
    font-size: 4rem;
}

.main .logo {
    padding: 2vh 0;
}

.main .menue {
    height: 50vh;
    margin: 0vh 20vw;
    border-width:2px;
    border-style:solid;
    border-color:#ffffff15;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.background .main Button {
    width: 99%;
    margin-bottom: 1vh;
}

.main .map {
    width: 99%;
    height: 100%;
    margin: 2%;
}

.main #logo {
    width: 7rem;
}